<template>
    <div class="wrapper">
      <div class="contain">
        <detail-box
            :source="movieData"
            :detailType="'movie'">
        </detail-box>
      </div>
    </div>
</template>
<script>
import api from '@/services/api'
import detailBox from '@/components/detailBox'

export default {
  name: 'movie',
  components: {
    detailBox
  },
  data () {
    return {
      movieData: {}
    }
  },
  created () {
    let id = this.$route.params.id
    api.getMovieSubject(id)
      .then(res => {
        this.movieData = res
      })
      .catch(res => console.log(res))
  }
}
</script>
<style scoped>
    .wrapper {
        width: 950px;
        margin: 0 auto;
    }
    .contain {
        min-height: 1000px;
        padding: 20px 0;
    }
</style>



