import resource from './resource'

export default {

  // 电影列表
  getNowMovieList: (params) => {
    return resource.get('/movie/in_theaters', params)
  },
  getComingMovieList: (params) => {
    return resource.get('/movie/coming_soon', params)
  },
  getTopList: (params) => {
    return resource.get('/movie/top250', params)
  },
  getWeeklyList: (params) => {
    return resource.get('/movie/weekly', params)
  },
  getUsBoxList: (params) => {
    return resource.get('/movie/us_box', params)
  },
  getNewMovieList: (params) => {
    return resource.get('/movie/new_movies', params)
  },

  // 电影条目信息
  getMovieSubject: (id, params) => {
    return resource.get('/movie/subject/' + id, params)
  },

  // 影人信息
  getCelebrity: (id, params) => {
    return resource.get('/movie/celebrity/' + id, params)
  }
}



