import axios from 'axios'
import qs from 'qs'

axios.defaults.baseURL = '/v2'

function processResponse (res) {
  if (res.status === 200) {
    return Promise.resolve(res.data)
  }

  return Promise.resolve(res.msg)
}

export default {
  get: (url, params = {}) => {
    return axios.get(url, { params }).then(res => processResponse(res))
  },

  put: (url, data = {}) => {
    return axios.put(url, qs.stringify(data)).then(res => processResponse(res))
  },

  del: (url, params = {}) => {
    return axios.delete(url, { params }).then(res => processResponse(res))
  },

  post: (url, data = {}) => {
    return axios.post(url, qs.stringify(data)).then(res => processResponse(res))
  }
}



