import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home'
import movie from '@/views/movie'
import celebrity from '@/views/celebrity'
import showAll from '@/views/showAll'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/movie/:id',
      name: 'movie',
      component: movie
    },
    {
      path: '/celebrity/:id',
      name: 'celebrity',
      component: celebrity
    },
    {
      path: '/showAll/:type',
      name: 'showAll',
      component: showAll
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



