<template>
    <el-row>
            <el-col :span="24">
                <div class="grid-content movie">
                    <div class="wrapper">
                        <div class="left-side">
                            <h2 class="block-title">
                                <router-link :to="{ name: 'movie', params: { id: 123 }}">电影</router-link>
                            </h2>
                            <div class="left-side-con">
                              <ul>
                                <li 
                                v-for="(item, index) in listType"
                                :class="{active: nowSelectList === index, noAllow: index > 2 }"
                                @click="changeMovieType(index)">
                                    <a>{{ item.title }}</a>
                                </li> 
                              </ul>
                            </div>
                        </div>
                        <div class="main">
                            <h2 class="block-title" style="font-size: 15px;">
                              {{ listType[nowSelectList]['title'] }}...
                              <router-link :to="{ name: 'showAll', params: { type: listType[nowSelectList]['key'] }}" class="show-all"> 查看全部</router-link>
                            </h2>
                            <div class="list">
                                <ul>
                                    <!-- 使用key用于强制替换元素/组件而不是重复使用它，从而能够懒加载而不是停顿加载 -->
                                    <li v-for="(item, index) in movieList" :key="item.id">
                                        <div class="pic">
                                            <router-link :to="{ name: 'movie', params: { id: item.id }}">
                                                <img
                                                class="pic-img" 
                                                v-lazy="item.images.medium"
                                                :alt="item.title">
                                            </router-link>
                                        </div>  
                                        <div class="img-title">
                                            {{ item.title }}
                                        </div>
                                        <el-rate
                                            v-model="rankVal[index]" 
                                            disabled
                                            allow-half
                                            show-text
                                            text-color="#ff9900"
                                            :text-template="(rankVal[index] * 2).toString()">
                                        </el-rate>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
</template>

<script>
import sidebarData from './movieSidebar'

export default {
  name: 'movieList',
  data () {
    return {
      movieList: [],
      rankVal: [],
      listType: sidebarData,
      nowSelectList: 0
    }
  },
  created () {
  },
  mounted () {
    this.changeMovieType(0)
  },
  methods: {
    changeMovieType (index) {
      this.nowSelectList = index

      this.listType[index]['request']({count: 8})
        .then(res => {
          this.movieList = res.subjects
          this.rankVal = []
          res.subjects.forEach(item => {
            this.rankVal.push(Number((item.rating.average / 2).toFixed(1)))
          })
        })
    }
  }
}
</script>

<style scoped>
    .left-side-con .active a {
        color: #00B51D !important;
    }
    .noAllow {
        cursor: not-allowed;
    }
    .show-all {
        color: #37a;
        font-size: 12px;
        font-weight: 400;
    }
</style>



