<template>
    <div>
        <el-row>
            <el-col :span="24">
                <div class="grid-content banner">
                    <div class="wrapper">
                    </div>
                </div>
            </el-col>
        </el-row>
        <movie-list></movie-list>
    </div>
</template>
<script>
import movieList from './movieList'
export default {
  name: 'home',
  components: {
    movieList
  },
  data () {
    return {
    }
  },
  created () {
  }
}
</script>
<style>
  .wrapper {
      position: relative;
      padding: 28px 0 4px;
      margin: 0 auto;
      overflow: visible;
      zoom: 1;
      width: 950px;
  }
  .banner {
      min-height: 36px;
      margin-bottom: 40px;
      background: #f1faf1 url('https://img3.doubanio.com/f/sns/463f29e6c00cdc1d15226bdcd3c86908526b9380/pics/sns/anony_home/doubanapp4_bg.png') no-repeat top right;
  }
  .movie {
      min-height: 36px;
      margin-bottom: 40px;
      background-color: #f7f7f7;
  }
  .banner .wrapper {
      height: 178px;
  }
  .movie .wrapper {
      padding-bottom: 20px;
      min-height: 400px;
  }
  .left-side {
      float: left;
      width: 100px;
      margin-right: 20px;
  }
  .block-title {
      color: #072;
      margin: 0 0 12px 0;
      line-height: 150%;
  }
  .left-side-con {
      line-height: 1.6;
  }
  .left-side-con a:link {
      color: #27a;
  }
  .left-side-con ul li {
      margin-top: 5px;
      cursor: pointer;
  }
  .main {
      width: 520px;
      overflow: hidden;
      zoom: 1;
  }
  .list ul li {
      display: inline-block;
      width: 120px;
      margin: 5px;
      text-align: center;
  }
  .list .pic {
      height: 145px;
      margin-bottom: 10px;
  }
  .list .pic img {
      width: 100px;
      border-width: 0;
      vertical-align: middle;
  }
  .list .img-title {
      line-height: 1.5;
      word-wrap: break-word;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      font-size: 13px;
  }
</style>



