<template>
    <div class="wrapper">
      <div class="contain">
        <detail-box
            :source="celebrityData"
            :detailType="'celebrity'">
        </detail-box>
      </div>
    </div>
</template>
<script>
import api from '@/services/api'
import detailBox from '@/components/detailBox'

export default {
  name: 'celebrity',
  components: {
    detailBox
  },
  data () {
    return {
      celebrityData: {}
    }
  },
  created () {
    let id = this.$route.params.id
    api.getCelebrity(id)
      .then(res => {
        this.celebrityData = res
      })
      .catch(res => {
        console.log(res)
      })
  }
}
</script>
<style scoped>
    .wrapper {
        width: 950px;
        margin: 0 auto;
    }
    .contain {
        min-height: 1000px;
        padding: 20px 0;
    }
</style>



