<template>
  <div class="wrapper">
    <div class="contain clearfix">
      <h1>{{ source[0].title }}</h1>
      <div class="list-con">
        <ol class="grid_view">
          <li v-for="(item, idx) in listData" :key="item.id">
            <div class="item">
              <div class="pic">
                <em>{{ idx + 1 + (index - 1) * 20 }}</em>
                <router-link 
                  :to="{name: 'movie', params: { id: item.id}}">
                  <img 
                    v-lazy="item.images.small" 
                    :alt="item.title">
                </router-link>
              </div>
              <div class="info">
                <div class="hd">
                  <router-link 
                    :to="{name: 'movie', params: { id: item.id}}">
                    <span class="title">{{ item.title }}/</span>
                    <span class="title">{{ item.original_title }}</span>
                  </router-link>
                </div>
                <div class="bd">
                  <p>
                    <span>导演:</span>
                    <span v-for="v in item.directors">{{ v.name }} </span>
                    <span>主演</span>
                    <span v-for="v in item.casts">{{ v.name }}</span>
                  </p>
                  <p>
                    <span>{{ item.year }}/</span>
                    <span v-for="v in item.genres">{{ v }} </span>
                  </p>
                  <p>
                    <el-rate
                        style="display: inline-block;vertical-align: top;"
                        :key="item.id"
                        v-model="rankVal[index]" 
                        disabled
                        allow-half
                        show-text
                        text-color="#ff9900"
                        :text-template="(rankVal[index] * 2).toString()">
                    </el-rate>
                    <span class="ga">{{ item.collect_count }} 人评价</span>
                  </p>
                </div>
              </div>
            </div>
          </li>
        </ol>
      </div>
      <div class="page" v-if="listData.length > 0">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="index"
          :page-size="count"
          layout="prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import titleName from '../home/movieSidebar'
export default {
  name: 'showAll',
  data () {
    return {
      source: [],
      listData: [],
      rankVal: [],
      count: 20,
      index: 1,
      total: null,
      start: 0
    }
  },
  created () {
    const route = this.$route
    let type = route.params.type
    this.index = Number(route.query.page) || 1
    this.start = (route.query.page - 1) * 20
    this.source = titleName.filter(item => item.key === type)
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      document.body.scrollTop = document.documentElement.scrollTop = 0
      this.source[0]['request']({start: this.start, count: this.count})
        .then(res => {
          this.total = res.total
          this.listData = res.subjects
          this.rankVal = []
          res.subjects.forEach(item => {
            this.rankVal.push(Number((item.rating.average / 2).toFixed(1)))
          })
        })
    },
    handleSizeChange (val) {
      this.handleCurrentChange(val)
    },
    handleCurrentChange (val) {
      this.index = val
      this.start = (val - 1) * 20
      this.$router.push({name: 'showAll', query: { page: val }})
      this.fetchData()
    }
  }
}
</script>
<style scoped>
    .wrapper {
        width: 950px;
        margin: 0 auto;
    }
    .contain {
        min-height: 1000px;
        padding: 20px 0;
    }
    h1 {
        word-wrap: break-word;
        display: block;
        font-size: 25px;
        font-weight: bold;
        color: #494949;
        margin: 0;
        padding: 0 0 15px 0;
        line-height: 1.1;
    }
    .list-con {
        float: left;
        width: 590px;
        padding-right: 40px;
    }
    .grid_view {
        border-top: 1px dashed #ccc;
        padding-left: 0;
    }
    .grid_view li {
        list-style: none;
        border-bottom: 1px dashed #ccc;
        padding: 12px 0;
    }
    .grid_view .pic {
        color: #bbb;
        float: left;
        padding-right: 25px;
    }
    .grid_view .pic em {
        float: left;
        padding: 0 10px;
    }
    a img {
        border-width: 0;
        vertical-align: middle;
    }
    .grid_view .info {
        overflow: hidden;
        zoom: 1;
    }
    .grid_view .info .title {
        font: 14px/150% Arial,Helvetica,sans-serif;
    }
    .info p {
        margin: 10px 0;
        color: #666;
        font-size: 12px;
    }
    .ga {
        display: inline-block;
        margin-top: 2px;
        margin-left: 10px;
    }
    .page {
        float: left;
        margin-top: 10px;
        margin-left: 60px;
        min-width: 310px;
    }

</style>



