<template>
    <div>
        <el-row type="flex" class="row-bg" justify="center">
        <el-col :span="4">
            <div class="grid-content">
            <a class="logo" href="">
            </a>
            </div>
        </el-col>
        <el-col :span="8">
            <div class="grid-content">
            <el-input
                placeholder="书籍、电影、音乐、小组、小站、成员"
                icon="search"
                v-model="input2"
                :on-icon-click="handleIconClick">
            </el-input>
            </div>
        </el-col>
        <el-col :span="6"><div class="grid-content"></div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
  data () {
    return {
      input2: ''
    }
  },
  methods: {
    handleIconClick (ev) {
      console.log(ev)
    }
  }
}

</script>

<style scoped>
  .logo {
    display: inline-block;
    width: 160px;
    height: 34px;
    background: url('https://img3.doubanio.com/f/sns/19886d443852bee48de2ed91f4a3bdfdaf8c809c/pics/nav/logo_db.png') no-repeat;
  }
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 20px 0 10px;
  }
</style>



