import api from '@/services/api'
export default [
  {
    title: '正在热映',
    key: 'hotShow',
    type: 0,
    request: function (params) {
      return api.getNowMovieList(params)
    }
  },
  {
    title: '即将上映',
    key: 'commingShow',
    type: 1,
    request: function (params) {
      return api.getComingMovieList(params)
    }
  },
  {
    title: 'Top250',
    key: 'top250',
    type: 2,
    request: function (params) {
      return api.getTopList(params)
    }
  },
  // 以下无权限
  {
    title: '口碑榜',
    key: 'Weekly',
    type: 3,
    request: function (params) {
      return api.getWeeklyList(params)
    }
  },
  {
    title: '北美票房榜',
    key: 'UsBox',
    type: 4,
    request: function (params) {
      return api.getUsBoxList(params)
    }
  },
  {
    title: '新片榜',
    key: 'newRank',
    type: 5,
    request: function (params) {
      return api.getNewMovieList(params)
    }
  }
]



