<template>
    <div class="box">
        <h1 v-if="detailType === 'movie'">
            {{ source.title }}
            <span class="year">({{ source.year }})</span>
        </h1>
        <h1 v-if="detailType === 'celebrity'">
            {{ source.name }}
            <span style="color:#888;">({{ source.name_en }})</span>
        </h1>
        
        <div class="movie-con">
            <div class="main-pic">
                <a href="" v-if="detailType === 'movie'">
                    <img 
                      :src="source.images.medium"
                      :alt="source.title">
                </a>
                <a href="" v-if="detailType === 'celebrity'">
                    <img 
                      :src="source.avatars.medium"
                      :alt="source.name">
                </a>
            </div>

            <div class="info" v-if="detailType === 'movie'">
                <span>
                    <span class="p1">导演:</span>
                    <span class="attr">
                        <a 
                        href="" 
                        v-for="item in source.directors">
                            {{ item.name }}
                        </a>
                    </span>
                </span><br/>
                <span>
                    <span class="p1">类型:</span>
                    <span class="attr">
                        <a 
                        href=""
                        v-for="item in source.genres">
                            {{ item }}
                        </a>
                    </span>
                </span><br/>
                <span>
                    <span class="p1">制作国家/地区:</span>
                    <span class="attr">
                        <a 
                        href=""
                        v-for="item in source.countries">
                            {{ item }}
                        </a>
                    </span>
                </span><br/>
                <span>
                    <span class="p1">主演:</span>
                    <span class="attr">
                        <router-link 
                        v-for="item in source.casts"
                        :key="item.id"
                        :to="{name: 'celebrity', params: { id: item.id }}"
                        >
                            {{ item.name }}
                        </router-link>
                    </span>
                </span><br/>
                <span>
                    <span class="p1">又名:</span>
                    <span class="attr">
                        <a 
                        href=""
                        v-for="item in source.aka">
                            {{ item }}
                        </a>
                    </span>
                </span>
            </div>
            <div class="info" v-if="detailType === 'celebrity'">
                <span>
                    <span class="p1">性别:</span>
                    <span class="attr">
                        <a 
                        href="">
                            {{ source.gender }}
                        </a>
                    </span>
                </span><br/>
                <span>
                    <span class="p1">出生地:</span>
                    <span class="attr">
                        <a 
                        href="">
                            {{ source.born_place }}
                        </a>
                    </span>
                </span><br/>
                <span v-if="source.aka.length">
                    <span class="p1">更多中文名:</span>
                    <span class="attr">
                        <a 
                        href="">
                            {{ source.aka }}
                        </a>
                    </span>
                </span><br/>
                <span v-if="source.aka_en.length">
                    <span class="p1">更多外文名:</span>
                    <span class="attr">
                        <a 
                        href="">
                            {{ source.aka_en }}
                        </a>
                    </span>
                </span><br/>
            </div>

        </div>
        <div class="movie-rank" v-if="detailType === 'movie'">
            <span class="f-z12">豆瓣评分</span><br />
            <div class="rank-box">
                <strong class="rank-num">{{ source.rating.average }}</strong>
                <div class="rank-right">
                    <el-rate
                        v-model="rankVal" 
                        disabled
                        allow-half
                        text-color="#ff9900">
                    </el-rate>
                    <span class="f-z12">{{ source.ratings_count }}人评价</span>
                </div>
            </div>
        </div>
        <div class="movie-abstru" v-if="detailType === 'movie'">
            <h2>{{ source.title }}的剧情简介 · · · · · ·</h2>
            <span class="summary">{{ minify(source.summary) }}</span>
        </div>
        <div class="movie-abstru" v-if="detailType === 'celebrity'">
            <h2>{{ source.name }}的剧情简介 · · · · · ·</h2>
            <span class="summary">暂无</span>
        </div>
        <div class="movie-abstru" v-if="detailType === 'movie'">
            <h2>{{ source.title }}的影人 · · · · · ·</h2>
            <ul class="movie-people">
                <li v-for="item in movieMan">
                  <router-link :to="{name: 'celebrity', params: { id: item.id }}">
                    <img 
                    v-lazy="item.avatars.medium">
                    <span class="f-z12 word-in-line">
                        {{ item.name.substr(0,8) }}
                    </span><br />
                    <span class="f-z12 color666">
                        {{ item.type }}
                    </span>
                  </router-link>
                </li>
            </ul>
        </div>
        <div class="movie-abstru" v-if="detailType === 'celebrity'">
            <h2>{{ source.name }}的图片· · · · · ·</h2>
        </div>
        <div class="movie-abstru" v-if="detailType === 'celebrity'">
            <h2>{{ source.name }}参演的影片· · · · · ·</h2>
            <ul class="movie-people">
                <li v-for="item in source.works">
                  <router-link :to="{name: 'movie', params: { id: item.subject.id }}">
                    <img 
                    v-lazy="item.subject.images.medium">
                    <span class="f-z12 word-in-line">
                        {{ item.subject.title.substr(0,8) }}
                    </span><br />
                    <span class="f-z12 color666">
                        {{ item.subject.year }}
                    </span>
                  </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { minify } from '@/utils'

export default {
  props: {
    source: {
      require: true,
      type: Object
    },
    detailType: {
      require: true,
      type: String
    }
  },
  data () {
    return {
    }
  },
  computed: {
    rankVal () {
      return Number((this.source.rating.average / 2).toFixed(1))
    },
    movieMan () {
      let directors = this.source.directors.map(item => ({
        alt: item.alt,
        avatars: item.avatars,
        id: item.id,
        name: item.name,
        type: '导演'
      }))
      let casts = this.source.casts.map(item => ({
        alt: item.alt,
        avatars: item.avatars,
        id: item.id,
        name: item.name,
        type: '主演'
      }))
      return directors.concat(casts)
    }
  },
  methods: {
    minify
  }
}
</script>
<style scoped>
    a:visited {
        color: #666699;
    }
    a:link {
        color: #37a;
    }
    a:visited {
        color: #669;
    }
    a:hover {
        color: #37a;
    }
    h1 {
        word-wrap: break-word;
        display: block;
        font-size: 25px;
        font-weight: bold;
        color: #494949;
        margin: 0;
        padding: 0 0 15px 0;
        line-height: 1.1;
    }
    .box {
        width: 590px;
        float: left;
    }
    .movie-people {
        overflow: hidden;
        margin-right: -20px;
        margin-top: 10px;
        line-height: 1.4;
    }
    .movie-people li {
        display: inline-block;
        margin-right: 20px;
        width: 80px;
        height: 150px;
        text-align: center;
    }
    .movie-people li img {
        margin-bottom: 5px;
        width: 80px;
    }
    .summary {
        font-size: 12px;
        text-indent: 2em;
        word-break: break-all;
        color: #111;
        line-height: 1.6;
    }
    .year {
        color: #888;
    }
    .movie-con {
        float: left;
        width: 415px;
    }
    .main-pic {
        float: left;
        text-align: center;
        margin: 1px 12px 0 0;
        max-width: 155px;
        overflow: hidden;
    }
    .main img {
        margin-bottom: 10px;
        width: 100px;
    }
    .info {
        float: left;
        width: 248px;
        word-wrap: break-word;
        font-size: 12px;
        line-height: 1.6;
    }
    .attr a {
        margin-right: 4px;
    }
    .movie-rank {
        float: left;
        width: 160px;
        margin: 2px 0 0 0;
        padding: 0 0 0 15px;
        border-left: 1px solid #eaeaea;
        color: #9b9b9b;
    }
    .rank-box {
        line-height: 2;
    }
    .rank-num {
        float: left;
        color: #494949;
        padding: 0;
        min-width: 30%;
        font-size: 28px;
    }
    .rank-right {
        float: left;
        padding: 10px 0 10px 6px;
    }
    .movie-abstru {
        float: left;
        width: 100%;
    }
    .movie-abstru h2 {
        color: #007722;
        margin: 24px 0 3px 0;
        font: 15px Arial, Helvetica, sans-serif;
        line-height: 150%;
    }
</style>



